*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  width: 100%;
}

.glcanvas {
  position: absolute;
  display: block;
  margin: 0 auto;
  z-index: -10;
  height: 100vh;
  width: 100%;
}
